
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineXSwitch from 'vux/src/components/inline-x-switch/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default{
name:"set",
components:{
XHeader,  InlineXSwitch,XSwitch,Group
},
data(){
return{
users:localStorage.getItem('users')?JSON.parse(localStorage.getItem('users')):'',
switcherchoice:true,
mobile:'',
border:'',
edit:false,
versionNumber:0,
value: false
}
},
mounted(){




if(window.plus){

}

localStorage.setItem("update","true");
var timer = setInterval(()=>{
if(localStorage.getItem("update")=="true"){
clearInterval(timer);
this.updateapp(0);
}
}, 500);
},
filters: {
regMobile: function (value) {
if (!value) return '';
var new_mobile ;
if (value.length > 7) {
new_mobile = value.substr(0, 3) + '****' + value.substr(7);
}
return new_mobile;
}
},
methods:{
handleChange(event) {

},
defalute(event){
event.preventDefault();
event.stopPropagation();
},
signout(){
this.$store.dispatch("signOut");
this.$router.push("/my/login");
},
focusnick(){
this.edit=true;
this.border='border';
},
blurnick(){
this.edit=false;
this.border='';
},
updateapp(gish) {
var server = 'api/api/config'; 
let apptype = "";
var BrowserInfo = {
userAgent: navigator.userAgent.toLowerCase(),
isAndroid: Boolean(navigator.userAgent.match(/android/ig)),
isIphone: Boolean(navigator.userAgent.match(/iphone|ipod/ig)),
isIpad: Boolean(navigator.userAgent.match(/ipad/ig)),
isWeixin: Boolean(navigator.userAgent.match(/MicroMessenger/ig)),
};
if(BrowserInfo.isIphone){
apptype = 1;

}else if(BrowserInfo.isAndroid){
apptype = 0;

};
this.$axios.post(server,`data=${JSON.stringify({apptype:apptype})}`).then((data)=> {
if (data.code == 1) {

var entity=data.result.appVersion;

if(entity>this.versionNumber){
mui.confirm(data.result.content, '版本更新', ["取消","立即更新"], (e) => {
if (e.index == 1) {
if(plus.os.name=="Android"){
this.downWgt(data.result.appUrl);  
this.versionNumber=entity;
}else{
if(data.result.appUrl.indexOf('wgt')>-1){
this.downWgt(data.result.appUrl);
this.versionNumber=entity;
}else{
plus.runtime.openURL("itms-apps://" + data.result.app_store_url);  
}
}
}/* else{
localStorage.removeItem("update");
} */
});
}else{
if(gish==1){
mui.toast('已是最新版本~');
localStorage.removeItem("update");
}
}
} else {
mui.toast('检测更新失败！');

}
});
},
clear_emory(){
mui.confirm("清理缓存会清理所有缓存数据，您确定要清理吗？","",["确认","取消"],(i)=>{
if(i.index==0){
this.$store.dispatch("signOut",{
});

localStorage.clear();
plus.storage.clear();
plus.cache.clear(()=>{
mui.toast("清理成功");
setTimeout(()=>{
this.$router.push("/index");
},500);
});
}
});
},
downWgt(url){//下载
let options = {filename:"_doc/update/"};
let dtask = plus.downloader.createDownload(url, options);
dtask.addEventListener('statechanged',(task, status)=>{
if(!dtask) return;
switch (task.state) {
case 0: //开始下载
this.download.isdownloading = true;
this.download.downstate = '即将开始';
break;
case 2: //已连接到服务器
this.download.downstate = '已连接到服务器';
break;
case 3: //已接收到数据
this.download.downstate = '正在下载';
this.download.currentsize = (task.downloadedSize / 1024 / 1024).toFixed(1) + 'MB';
this.download.totalsize = (task.totalSize / 1024 / 1024).toFixed(1) + 'MB';
this.download.progress = Math.round((task.downloadedSize / task.totalSize) * 100) + '%';
break;
case 4: //下载完成
this.download.downstate = '下载完成';
this.download.isdownloading = false;
this.installWgt(task.filename); 
break;
}
});
dtask.start();
},  installWgt(path){//安装
plus.nativeUI.showWaiting("正在安装...");
plus.runtime.install(path,{},function(){
plus.nativeUI.closeWaiting();

plus.nativeUI.alert("更新完成！",function(){
plus.runtime.restart();
});
localStorage.removeItem("update");
},function(e){
plus.nativeUI.closeWaiting();

plus.nativeUI.alert("安装失败["+e.code+"]："+e.message);

});
},

},

}



